import React from 'react';
import Footer from '../../components/footer/footer';
import Dashboard from '../../components/dashboard/dashboard';
import Select from 'react-select';
import FireBase from '../../components/firebase/FireBase';
import * as firebase from 'firebase';
import './main.scss';


FireBase.init();

class Main extends React.Component {
  constructor() {
    super();
    this.database = firebase.database().ref().child('JSONData');
    this.state = {
    selectedOption: {
      value: ''
    },
    database: null,
  }
  }

  componentDidMount = () => {
    this.database.on('value', (snap) => {
      this.setState({
        database: snap.val(),
      })
    });
  }

  

  handleChange = (selectedOption) => {
    this.setState({ selectedOption });
  }

  render() {
    const { selectedOption, database } = this.state;
    return (
      <div className='mainfrapper'>
        <header className = 'header'>
          <div className='header-search-bar'>
          {
            database && <Select value={selectedOption} onChange={this.handleChange} options={database.listSelect}/>
          }
          </div>           
        </header>
        {
          database && selectedOption.value && <Dashboard mentor={selectedOption.value} database={database} />
        }   
        <Footer />
      </div>
    );
  }
}

export default Main;
