import React, { Component } from 'react';
import './dashboard.scss';

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.tasks = props.database.tasks;
    this.className = null;
  }

    getClassName(status) {
        switch(status) {
            case 'Checked':
            this.className = 'dashboard-status-red';
            break;
            case 'Checking':
            this.className = 'dashboard-status-green';
            break;
            case 'In Progress':
            this.className = 'dashboard-status-yellow';
            break;
            case 'ToDo':
            this.className = 'dashboard-status-grey';
            break;
            default:
            break;
        }
        return this.className;
    }

  render() {
      const { database, mentor } = this.props;
      const arrayOfTasks = Object.keys(database.tasks);
      const arrayOfMentorStudents =  Object.keys(database.mentors[mentor]);
      arrayOfMentorStudents.forEach( (item) => {
        arrayOfTasks.forEach( (task) => {
          if(!database.mentors[mentor][item][task]) {
            database.mentors[mentor][item][task] = database.tasks[task].status;
          }
        })
      })

      console.log("1.arrayOfTasks ");
      console.log(arrayOfTasks);
      console.log("2.arrayOfMentorStudents ");
      console.log(arrayOfMentorStudents);
      console.log("3.mentor ");
      console.log( database.mentors[mentor]);

      return (
        <section className = "dashboard">
        <table className = "dashboard-table" id="tableDashboard">
          <tbody>
            <tr>
              <td className = 'dashboard-cell'></td>
              {arrayOfMentorStudents.map( (item)  => {
                return (
                  <td className = 'dashboard-cell'>{item}</td>
                )
              })}
            </tr>
          {arrayOfTasks.map( (task) => { 
            return (
              <tr>
                <td className = 'dashboard-cell'>{database.tasks[task].name}</td>
                {arrayOfMentorStudents.map( (item)  => {
                  return(
                    <td className = {'dashboard-cell ' + this.getClassName(database.mentors[mentor][item][task])}>{database.mentors[mentor][item][task]}</td>
                  )
                })}
              </tr>
            )
          })}
        </tbody>
      </table>    
    </section>    
    );
  }

}

export default Dashboard;
